/*---------------
    logo
---------------*/


.logo {
    height: 60px;
    line-height: 60px;
    position: absolute;
    top: 0;
    left: 0;
    width: 240px;
    z-index: 100;
    padding-left: 20px;
}


.logo a {
    font-size: 22px;
    color: #fff;
    margin: 0;
    text-decoration: none;
    display: inline-block;
    float: none;
}


.logo a i {
    font-size: 24px;
    color: #7cd8a9;
    padding-right: 5px;
}

.logo a .brand-name {
    float: right;
}

.logo a i, .logo a img {
    display: inline;
    vertical-align: middle;
    margin-right: 10px;
}

.icon-logo {
    display: none;
}

.icon-logo a {
    color: #7cd8a9;
}

.icon-logo a:hover {
    color: #fff;
}

.sidebar-collapsed .icon-logo {
    height: 60px;
    padding-top: 5px;
    line-height: 45px;
    display: block !important;
    position: absolute;
    left: -52px;
    width: 52px;
    font-size: 24px;
    text-align: center;
}


/*---------------------
    sidebar left
-----------------------*/

.sidebar-left {
    width: 240px;
    position: absolute;
    top: 50px;
    left: 0;
}

.sticky-sidebar  {
    position: fixed;
    height: 100%;
    overflow-y: auto;
    z-index: 100;
}

.sidebar-collapsed .sticky-sidebar {
    overflow-y: visible;
    position: absolute;
}


.sidebar-left-info {
    padding: 0px;
}

.sidebar-left .search-content {
    display: none;
}

.sidebar-left .search-content::after {
    content: '';
    display: block;
    clear: both;
}

.sidebar-left .search-content input {
    padding: 10px;
    width: 90%;
    margin: 20px 10px 20px 12px;
    border-radius: 30px;
    border: none;
}

.sidebar-left .search-content input:focus {
    width: 90%;
}

.noti-arrow {
    margin-right: 15px;
    margin-top: 2px;
    font-weight: normal;
}

.side-navigation {
    margin-bottom: 10px;
    margin-top: 1px;
}

.navigation-title {
    font-size: 11px;
    font-weight: normal;
    margin: 0;
    padding: 10px 20px;
    text-transform: uppercase;
    letter-spacing: 2px;
}

.side-navigation > li > a {
    color: #fff;
    padding:15px 20px;
    border-radius: 0;
}

.side-navigation > li > a:hover,
.side-navigation > li > a:active {
    /*background-color: #000;*/
    border-radius: 0;
    -webkit-border-radius: 0;
}

.side-navigation > li.menu-list > a {
    position: relative;
}

.side-navigation > li.nav-active > ul {
    display: block;
}

.side-navigation > li.menu-list > a:after {
    content: "\f105";
    display: inline-block;
    font-family: "FontAwesome";
    padding-right: 20px;
    position: absolute;
    right: 0;
}


.side-navigation > li.nav-active > a:after {
    content: "\f107";
    display: inline-block;
    font-family: "FontAwesome";
    padding-right: 20px;
    position: absolute;
    right: 0;
}


.side-navigation > li.menu-list.active > a:after,
