-----------------------*/

.state-overview .symbol, .state-overview .value {
    display: inline-block;
    text-align: center;

}

.state-overview .value  {
    float: right;
    text-align: left;
}

.state-overview .value h1, .state-overview .value p  {
    margin: 0;
    padding: 0;
}

.state-overview .value h1 {
    font-weight: 300;
}

.state-overview .symbol i {
    font-size: 25px;
}

.state-overview .symbol {
    width: 30%;
    padding: 50px 15px;
}

.state-overview .value {
    width: 68%;
    margin-top: 30px;
    border-left: 1px solid;
    padding-left: 10%;
}

.state-overview .value.gray {
    border-color:#eaeaeb ;
}
.state-overview .value.white {
    border-color:rgba(255,255,255,.2) ;
}

/*----------------------*/

.state-overview .green {
    background: #53d192;
    color: #fff;
}

.state-overview .purple {
    background: #a979d1;
    color: #fff;
}

.state-overview .red {
    background: #ff6c60;
    color: #fff;
}

.state-overview .yellow {
    background: #f8d347;
    color: #fff;
}

.state-overview .blue {
    background: #57c8f2;
    color: #fff;
}


/*-------------------------------
    text color
--------------------------------*/


.green-color, .text-success {
    color: #53d192;
}

.purple-color, .text-primary {
    color: #a979d1;
}

.blue-color, .text-info {
    color: #6bd3f3;
}

.yellow-color, .text-warning {
    color: #ffd200;
}

.red-color, .text-danger {
    color: #e55957;
}


/*---------------------
    boxed view
-----------------------*/

.boxed-view {
    background: #5ac29c;
}

.boxed-view .container{
    position: relative;
    background: #32323a;
    padding: 0;
}

.boxed-view .container .header-section .logo{
    display: none;
}

.boxed-view .container .sidebar-left{
    top: 0px;
}

.boxed-view .container  .side-navigation {
    margin-top: 60px;
}

.boxed-view .right-notification {
    margin-right:0px;
}


/*---------------------
    tools
-----------------------*/


.tools {
    margin: -5px;
}
.tools a {
    border-radius: 2px;
    color: #C5C5CA;
    float: left;
    padding: 10px;
    text-decoration: none;
    font-size: 11px;
}

.tools a:hover {
    background: #7cd8a9;
    color: #fff;
}


.refresh-block{
    width: 100%;
    height: 100%;
    background-color: rgba(255,255,255,.8);
    -webkit-transition: all .05s ease;
    transition: all .05s ease;
    top: 0px;
    left: 0px;
    position: absolute;
    z-index: 1000;
    border-radius: 2px;
}

.refresh-block .refresh-loader{
    display: inline-block;
    position: absolute;
    text-align: center;
    top: 50%;
    left: 50%;
    margin-left: -16px;
    margin-top: -16px;
}
.refresh-block .refresh-loader i{
    display: inline-block;
    line-height: 32px;
    color: #000;
    font-size: 16px;
}

/*---------------------
    footer content
-----------------------*/


footer {
    background: #e9e9eb;
    padding: 15px 20px;
    color: #232425;
    font-size: 12px;
    position: absolute;
    bottom: 0;
    width: 100%;
}

footer.sticky-footer {
    position: fixed;
    bottom: 0;
    width: 100%;
}




/*---------------------------
    mobile visit
----------------------------*/

.mobile-visit {
    margin: 0px;
    padding-left: 0;
}


.mobile-visit .page-view-label{
    margin-top: 18px;
}


.mobile-visit li {
    padding: 35px 0px;
    margin-left: 4%;
    list-style: none;
    float: left;
    width: 33%;
}

.mobile-visit li:first-child{
    width: 20%;
}

.easy-pie-chart {
    display: inline-block;
    padding:0 20px 20px 0   ;
}

.easy-pie-chart .iphone-visitor {
    color: #43c584;
}

.easy-pie-chart .android-visitor{
    color: #a979d1;
}

.visit-title {
    display: inline-block;
    color: #555555;
}

.visit-title i {
    font-size: 20px;
    padding-right: 10px;
}

.visit-title i,
.visit-title span {
    float: left;
}


/*---------------------------
    monthly page view
----------------------------*/

.monthly-page-view {
    margin: 0px;
    padding-left: 0;
}

.monthly-page-view li {
    padding: 20px 0px;
    list-style: none;
}

.monthly-page-view li:last-child .chart {
    margin-top: 10px;
}

.page-view-label span {
    display: block;
    color: #bdc1c3;
}
.page-view-value {
    font-size: 26px;
    color: #555555 !important;
    font-weight: 400;
    margin-bottom: 10px;
}



/*------------------
    team member
-------------------*/

.post-wrap aside {
    display: table-cell;
    float: none;
    height: 100%;
    padding: 0;
    vertical-align: top;
}

.pro-box {
    border-collapse: collapse;
    border-spacing: 0;
    display: table;
    table-layout: fixed;
    width: 100%;
}


.team-member .action-set a,
.team-member .team-title a{
    color: #fff;
}

.team-member .action-set,
.team-member .team-title,
.team-member .sub-title {
    display: inline-block;
    width: 100%;
}

.team-member .action-set {
    margin-top: 30px;
    margin-bottom: 20px;
}

.team-member .m-name {
    margin-bottom: 5px;
    font-size: 16px;
    font-weight: 600;
}

.team-member .sub-title {
    font-size: 13px;
    color: rgba(255,255,255,.5);
}

.team-member .call-info {
    margin: 72px 0;
}

.team-member .call-info a {
    width: 38px;
    height: 38px;
    line-height: 38px;
    text-align: center;
    border: 1px solid #fff;
    border-radius: 50%;
    color: #fff;
    display: inline-block;
}

.team-member .call-info a:hover {
    background: #fff;
}

.team-member .call-info a i {
    font-size: 14px;
}

.team-member .call-info img {
    width: 100px;
    border-radius: 50%;
    margin:10px;
}

.team-member .status {
    margin: 30px 0;
    display: inline-block;
}

.team-member .status h5 {
    font-size: 14px;
    text-transform: uppercase;
    font-weight: bold;
    margin-bottom: 5px;
}

.team-member .status span {
    font-size: 13px;
}

.team-list {
    padding: 0;
    margin:0;
    list-style: none;
}

.team-list li a {
    display: inline-block;
    width: 100%;
    padding:14px 15px;
    border-bottom: 1px solid #f3f3f3;
    color: #333;
}

.team-list li:hover {
    background: #fafafa;
}

.team-list li a span {
    display: inline-block;
    margin-right: 10px;
}

.team-list li .thumb-small {
    width: 50px;
    height: 50px;
    display: inline-block;
    position: relative;
}

.team-list li .thumb-small .dot {
    width: 12px;
    height: 12px;
    display: inline-block;
    position: absolute;
    background: #e6e6e6;
    border-radius: 50%;
    top: auto;
    right: 0;
    bottom: 0;
    left: auto;
    border: 2px solid #fff;
}

.team-list li .thumb-small .dot.online {
    background: #5cc691;
}

.team-list li .thumb-small .dot.away {
    background: #ffd200;
}

.team-list li .thumb-small .dot.busy {
    background: #ff6a6a;
}

.team-list li .thumb-small .dot.offline {
    background: #e6e6e6;
}

.team-list li .thumb-small img {
    width: 100%;
    height: auto;
}

.circle {
    border-radius: 50%;
}

.team-list li .name {
    margin-top: 15px;
    display: inline-block;
}

.add-more-member {
    padding: 18px 15px;
}

.add-more-member a, a.light-color {
    color: #bdc1c3;
}

.add-more-member a:hover, a.light-color:hover {
    color: #2b2b2c;
}

a.add-btn {
    padding:4px 10px;
    color: #fff;
    display: inline-block;
    margin-top: -3px;
}


a.add-btn:hover {
    color: #fff;
}

a.add-btn {
    background: #9c78cd;
}


.team-member .call-info a:hover {
    color: #a389d3;
}

.action-tools a {
    color: #c5c5ca;
}


