/*------------------
    sale monitor
-------------------*/

.sale-monitor .title{
    margin: 0 0 40px 0;
}

.sale-monitor .title h3{
    font-size: 14px;
    text-transform: uppercase;
    color: #555;
    margin: 20px 0 10px 0;
}

.sale-monitor .title p{
    font-size: 14px;
