}


/*---------------------------
    dashboard v map
----------------------------*/


.w-map-size {
    width: 100%;
    height: 300px;
    position: relative;
    margin-top: 10px;
}

.jvectormap-zoomin, .jvectormap-zoomout,
.jqvmap-zoomin, .jqvmap-zoomout {
    border:1px solid #eaeaea;
    background: #fff;
    border-radius: 0;
    color: #545454;
    width: 28px;
    height: 28px;
    line-height: 20px;
    text-align: center;
    cursor: pointer;
}

.jvectormap-zoomin:hover, .jvectormap-zoomout:hover,
.jqvmap-zoomin:hover, .jqvmap-zoomout:hover {
    background: #eaeaea;
}

.jvectormap-zoomin, .jqvmap-zoomin {
    position: absolute;
    z-index: 70;
    top: 0px;
}

.jvectormap-zoomout, .jqvmap-zoomout {
    position: absolute;
    z-index: 70;
    top: 27px;
}


/*---------------------------
    flot chart
----------------------------*/


#flotTip {
    background: rgba(0,0,0,0.8);
    padding: 5px 10px;
    border-radius: 3px;
    color: #fff;
}

.pieLabel div {
    font-size: 11px !important;
