    margin:0 auto;
}

.earning-chart-info {
    padding: 15px  0 0 0
}

.earning-chart-info h4{
    /*margin-top: 0;*/
    margin-bottom: 5px;
}

.series-list input {
    margin-right: 10px;
}

.f-c-space {
    width: 100%;
    height:280px;
