}

/*---------------------------
    code high light style
----------------------------*/


.highlight pre code {
    color: #333333;
    font-size: inherit;
}

.nt {
    color: #2F6F9F;
}

.na {
    color: #4F9FCF;
}

.s {
    color: #D44950;
}

.c {
    color: #999999;
}


.r-close-btn {
    position: absolute;
    right: 10px;
    color: #fff;
    top: 15px;
    cursor: pointer;
    width: 30px;
    height: 30px;
    text-align: center;
    line-height: 30px;
    border-radius: 2px;
    -webkit-transition:all 0.3s;
    -moz-transition:all 0.3s;
    transition:all 0.3s;
}

.r-close-btn:hover {
    color: #fff;
}


/*---------------------------
    right side bar
----------------------------*/

.sb-toggle-right {
    width: 55px;
    height: 60px;
    line-height: 30px;
    font-size: 14px;
    padding: 15px;
    cursor: pointer;
    text-align: center;
    color: #384246;
    -moz-transition: all 0.2s ease-out 0s;
    -webkit-transition: all 0.2s ease-out 0s;
    transition: all 0.2s ease-out 0s;
}

.sb-toggle-right:hover {
    background: #f7f7f7;
}


.sb-slidebar {
    background-color: #fff !important;
    /*margin-top: 60px;*/
    border-left: 1px solid #e9e9e9;
    color: #2b2b2c;
}
.sb-slidebar .side-title {
    padding: 10px 0px;
    text-transform: uppercase;
    color: #FF6C60;
}

.right-bar .tab-content {
    padding: 20px;
}

.sb-slidebar .tab-content>.active {
    display: none;
    visibility: hidden;
}

.sb-slidebar.sb-active .tab-content>.active {
    display: block;
    visibility: visible;
}

.right-bar .nav-tabs li a,
.right-bar .nav-tabs li a:hover,
.right-bar .nav-tabs li.active a {
    padding: 20px 15px;
    border-radius: 0;
    border: none;
}

.right-bar .nav-tabs li a {
    color: #fff;
    margin-right: 0px;
}

.right-bar .nav-tabs li a:hover,
.right-bar .nav-tabs li.active a {
    color: #2b2b2c;
    background: #fff;
}

.right-bar .nav-tabs {
    background: #2b2b2c;
    color: #fff;
}


/*---------------
    chat
----------------*/

.chat-list {
    position: relative;
    margin-bottom: 20px;
}

.chat-list h3{
    font-size: 14px;
    text-transform: uppercase;
    color: #000;
    margin: 0 0 5px;
}

.chat-list h5{
    font-size: 12px;
    color: #a5a5a5;
    margin: 0;
}

.chat-list a {
    color: #323232;
}

.chat-list .add-people {
    position: absolute;
    right: 0;
    top: 5px;
}

.side-title {
    display: table;
    margin: 0 0 20px;
    overflow: hidden;
}
.side-title h2  {
    float: left;
    padding: 0 10px 0 0;
    margin: 0;
    font-size: 14px;
    text-transform: uppercase;
    color: #2b2b2c;
    font-weight: bold;
}

.side-title .title-border-row {
    position: relative;
    display: table-cell;
    vertical-align: middle;
    height: 1px;
    width: 100%;
}

.side-title .title-border {
    height: 1px;
    border-top: 1px solid #e8e8e8;
    position: relative;
    display: block;
    width: 100%;
    box-sizing: content-box;
    -webkit-box-sizing: content-box;
    -moz-box-sizing: content-box;
    -o-box-sizing: content-box;
}

.team-list.chat-list-side li:hover {
    background: #f3f3f3;
}

.team-list.chat-list-side li .thumb-small {
    width: 30px;
    height: 30px;
}

.team-list.chat-list-side li .thumb-small {
    margin-top: 5px;
}

.team-list.chat-list-side li .thumb-small .dot {
    right: -3px;
    top: 0px;
}

.team-list.chat-list-side li .name {
    margin-top: 0;
    font-size: 14px;
    color: #2b2b2c;
}

.inline small {
    display: block;
}

.team-list.chat-list-side {
    margin: 0 -20px 20px -20px;
}

.team-list.chat-list-side li a {
    display: table;
    padding: 10px 20px;
    border: none;
    position: relative;
}

.team-list.chat-list-side li .thumb-small ,
.team-list.chat-list-side li .inline {
    display: table-cell;
    float: none;
    vertical-align: middle;
}

.team-list.chat-list-side li .inline {
    padding-left: 20px;
    text-align: left;
}

.team-list.chat-list-side li .settings {
    position: absolute;
    right: 10px;
    top: 16px;
}


.right-bar .tab-pane .team-list.chat-list-side:last-child,
.right-bar .tab-pane .aside-widget:last-child {
    padding-bottom: 100px;
}

.right-bar .tab-pane .aside-widget .team-list.chat-list-side:last-child {
    padding-bottom: 0px;
}


.right-bar .tab-pane .aside-widget{
    margin: 0px -20px;
    padding: 0px 20px;
}


