/*-------------------
    weather widget
--------------------*/

.weather-widget {
    width: 100%;
    float: left;
    background: #fff;
    margin-bottom: 20px;
}

.weather-widget .weather-state {
    background-color: #00c4a9;
    background-image: url(../img/weather_background.jpg);
    background-size: cover;
    height: 261px;
    width: 45%;
    text-align: center;
}

.weather-widget .weather-state .weather-icon {
    font-size: 60px;
    color: #fff;
    display: block;
    margin: 20px 0 60px 0;
}
.weather-widget .weather-state .weather-type {
    display: block;
    text-align: center;
    padding: 20px 0;
    font-size: 28px;
    color: #fff;
    font-weight: 300;
    line-height: 28px;
}

.weather-widget .weather-state,
.weather-widget .weather-info {
    float: left;
