    top: -40px;
    font-size: 20px;
    font-weight: 400;
}

.weather-widget .weather-info .weather-city {
    color:#bdc1c3;
    font-size: 20px;
    font-weight: 300;
    margin-top: 20px;
    display: block;
}

.weather-widget .weather-info {
    position: relative;
}

.weather-widget .weather-info .switch-btn {
    position: absolute;
    right: 20px;
    top: 30px;
}


/*----------------------------
        login page
------------------------------*/

.login-body {
    background-color: #2b2b2d;
}

.login-body .login-logo {
    margin: 0;
    text-align: center;
    background: #1b1b1d;
    color: #fff;
    font-size: 18px;
    text-transform: uppercase;
    display: inline-block;
    width: 100%;
    padding: 50px 0;
}

.form-signin {
    max-width: 330px;
    margin: 50px auto 0;
}

h2.form-heading {
    margin: 0;
    padding:30px 15px;
    text-align: center;
    background: #222224;
    color: #fff;
    font-size: 18px;
    text-transform: uppercase;
    display: inline-block;
    width: 100%;
}

.form-signin .checkbox {
    margin-bottom: 14px;
}

.radios {
    display: inline-block;
    margin-bottom: 15px;
    width: 100%;
}

.radios .col-sm-6, .radios .col-lg-6 {
    padding-left: 0;
}

.form-signin .checkbox, .radios label {
    font-weight: normal;
    color: #565658;
}


.form-signin .checkbox input[type="checkbox"],
.radios input[type="radio"]{
    margin-right: 5px;
}

.form-signin .form-control {
    position: relative;
    font-size: 16px;
    height: auto;
    padding: 10px;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
}

.form-signin .form-control:focus {
    z-index: 2;
}

.form-signin input[type="text"], .form-signin input[type="password"] {
    margin-bottom: 15px;
    border-radius: 4px;
    border: none;
    background: #222224;
    box-shadow: none;
    font-size: 13px;
    color: #fff;
    padding: 12px;
}


.form-signin p {
    /*text-align: center;*/
    color: #b6b6b6;
    font-size: 14px;
}

.modal-body p {
    color: #333;
}

.form-signin a, .registration, .login-body label  {
    color: #565658;
}

.registration a {
    color: #fff;
