}

.twt-feed {
    background: #f3f3f3;
    padding: 20px;
    text-align: center;
    position: relative;
}
.twt-feed:after {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    border-right: 5px solid #fff;
    border-bottom: 45px solid #fff;
    border-left: 45px solid #27baff;
    border-top: 6px solid #27baff;
    transform: rotate(-180deg);
    -ms-transform: rotate(-180deg);
    -webkit-transform: rotate(-180deg);
}

.twt-feed:before {
    content: "\f099";
    font-family: fontawesome;
    left: 25px;
    position: absolute;
    top: 25px;
    z-index: 100;
    font-size: 18px;
    color: #fff;
}

.twt-feed img {
    width: 80px;
    height: 80px;
    border-radius: 50%;
    border: 5px solid #fff;
}

.twt-feed h2, .twt-feed h2 a {
    font-size: 22px;
    text-transform: uppercase;
    color: #333;
}

.twt-feed a {
    color: #27baff;
}


