/*-------------------------------
        slick carousel
--------------------------------*/


.slick-carousal {
    background: url("../img/news_image.jpg") no-repeat;
    padding: 20px 10px;
    color: #fff;
    position: relative;
    width: 100%;
    height:278px;
    background-size: cover;
}

.overlay-c-bg {
    background: rgba(68,68,68,.9);
    width: 100%;
    height: 278px;
    position: absolute;
    left: 0;
    top: 0;
}

.slick-carousal h1 {
    text-align: center;
    font-size: 14px;
    margin: 40px 20px;
    line-height: 20px;
    font-weight: normal;
    font-style: italic;
}

.slick-carousal h3, .slick-carousal .date {
    text-align: center;
    font-size: 12px;
    margin: 5px;
    font-weight: normal;
    text-transform: uppercase;
}

.slick-carousal .date {
    display: block;
    margin: 0;
}

a.view-all {
    color: #fff;
    border:1px solid rgba(255,255,255,0.20);
    padding: 5px 15px;
    text-align: center;
    border-radius: 25px;
    -webkit-border-radius: 25px;
    margin-bottom: 18px;
    display: inline-block;
    text-transform: uppercase;
    font-size: 12px;
    margin-top: 20px;
}

a.view-all:hover {
    background: rgba(255,255,255,0.20);
    color: #000;
}


.slick-carousal #owl-demo .item img{
    display: block;
    width: 100%;
    height: auto;
}

.slick-carousal .owl-buttons {
    position: absolute;
    top: 50%;
    margin-top: -8px;
    width: 100%;

}

.slick-carousal .owl-carousel .owl-item {
    overflow: hidden;
}

.slick-carousal .owl-prev, .owl-next {
    position: absolute;
}

.slick-carousal .owl-next {
    right: 0;
}

.slick-carousal .owl-buttons .owl-prev {
    text-indent: -9999px;
    background: url("../img/left-arrow.png") no-repeat;
    width: 16px;
    height: 16px;
    display: inline-block;
}

.slick-carousal .owl-buttons .owl-next {
    text-indent: -9999px;
    background: url("../img/right-arrow.png") no-repeat;
    width: 16px;
    height: 16px;
    display: inline-block;
}

